@import 'global/vars.scss';
@import 'global/mixin.scss';

 header { 
 
 background: url(/images/background/image_heder.jpg) no-repeat; 
 background-size: cover;

}

 .wrapper-header { 

    flex-wrap: wrap;
    @include size;  
 }

.header-body {

    width: 100%;
    align-items:center;
    justify-content: space-between;
    padding: 30px 0;

    .menu-header-top {

        ul.menu-top { padding: 0 20px;
        
            a {
                color: $color-white;
                padding-bottom: 5px;
                position: relative;
                
                &:after {

                    content: '';
                    position: absolute;
                    top: 100%;
                    left: 0;
                    width: 100%;
                    border: 1px solid $color-white;
                    opacity: 0;
                    transition: opacity .3s,transform .3s;
                    transform: translateY(10px);
                }

                &:hover:after { 

                    opacity: 1;
                    transform: translateY(-4px);
                }
            }
        }   
    }
}

.main-navigation {

    width: 100%;
    background: url(/images/background/pattern_blue.png);

    ul.menu { width: 1060px; 
    
        li.menu-item  { 

            transition:  all 0.3s;
            position: relative;

            &:hover > ul { display: block; }
        
             &:before {

                content: '•';
                display: inline-block;
                vertical-align: middle;
                @include font($size: 12px); 
                
            }

            ul { display: none; }

            &:hover:before { color: transparent; }

            &:hover + li:before { color: transparent; }
            
            &:hover  { background-color: $color-blue2; }

            &:first-of-type:before {color: transparent; } 
        }

        a {

            @include font($size: 16px);
            padding: 18px 15px;
        }

        li ul {
        
            min-width: 230px;
            background-color: $color-blue2;
            position: absolute;
            top: 100%;
            margin: 0;
            z-index: 2;

            li.menu-item:hover { background-color: #2f8fc8; }
            
            li.menu-item::before { font-size: 0; }

            li ul { 
                
           
                left: 100%;
                top: 0;     
            }
        }
    }
}

.search-form {

    width: 250px;
    flex-wrap: wrap;

   .searchstring {
   
        width: calc(100% - 65px);
        padding: 10px;
        background: #eff8fd;
        border-radius: 0;
        @include font($font-sans,  rgba(73, 110, 134, 0.5));
         
        &::-moz-placeholder { 

            @include font($font-sans,  rgba(73, 110, 134, 0.5));
            opacity: 1; 
         }

        &::-webkit-input-placeholder { @include font($font-sans,  rgba(73, 110, 134, 0.5));
            opacity: 1; 

        }   
    }
}

.searchsubmit {
   
        width: 40px;
        height: 40px;
        @include font($font-awesome, $color-white);
        vertical-align: top;
        border: 1px solid #eff8fd;
        background-color: $color-blue2;
        border-radius: 0;
        cursor: pointer;
}

.sign-search {

    @include font($size: 12px);
    padding-top: 5px;
}

.slicknav_btn {

    position: relative;
    display: none;
    padding: 0.438em 0.625em 0.438em 0.625em;
    cursor: pointer;
    @include font($font-sansbold, $color-white);


        &:before {
        content: "\f0c9";
        @include font($font-awesome, $color-white);
        padding-right:10px
    }
}
